import * as React from 'react';
const connect = require('react-redux').connect;

import NormalQuestion from "../components/normal_question"

import Easy from '../code_examples/easy';

var seed = require('seed-random');


function mapStateToProps(state) {
  return {
  };
}

function mapDispatchToProps(dispatch) {
  return {
  };
}

class Test extends React.Component<any, any> {
  code_master:string

  shuffled_real_names:Array<string>

  constructor(props){
    super(props)

    seed(this.questionNumber(), {global: true});

    this.shuffled_real_names = this.shuffleArray(this.realNames())
  }

  realNames(){
    return this.getParameterByName("player_names").split(",")
  }

  questionNumber(){
    return parseInt(this.getParameterByName("question_number"))
  }

  accepted(){
    return this.stringToBool(this.getParameterByName("accepted"))
  }

  rejected(){
    return this.stringToBool(this.getParameterByName("rejected"))
  }

  stringToBool(str){
    if(str == "true") return true
    if(str == "false") return false

    return undefined
  }

  backendResource(){
    return this.getParameterByName("backend_resource")
  }

  getParameterByName(name) {
      var url = window.location.href;
      name = name.replace(/[\[\]]/g, "\\$&");
      var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
          results = regex.exec(url);
      if (!results) return null;
      if (!results[2]) return '';
      return decodeURIComponent(results[2].replace(/\+/g, " "));
  }

  remapNames(question, real_names){
   
    var fake_names = [
      "Alice",
      "Bree",
      "Carry",
      "Deanna"
    ]

    for(var i in fake_names){
      question.code = question.code.replace(new RegExp(fake_names[i],"g"), this.shuffled_real_names[i])
    }

    return question   
  }

  codeMaster(){
    return this.shuffled_real_names[Math.min(4, (this.realNames().length-1))]
  }

  shuffleArray(array) {
    for (var i = array.length - 1; i > 0; i--) {
      var j = Math.floor(Math.random() * (i + 1));
      var temp = array[i];
      array[i] = array[j];
      array[j] = temp;
    }
    return array;
  }

  randomInt(){
    return Math.floor(Math.random() * 10)
  }


  render(){
    return <NormalQuestion backend_resource={this.backendResource()} accepted={this.accepted()} rejected={this.rejected()} code_master={this.codeMaster()} question={this.remapNames(Easy[this.questionNumber()], this.realNames())} />
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Test);



