import * as React from 'react';
const connect = require('react-redux').connect;
import AppBar from 'material-ui/AppBar';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import RaisedButton from 'material-ui/RaisedButton';
import FlatButton from 'material-ui/FlatButton';
var brace = require('brace')
import AceEditor from 'react-ace';
import Paper from 'material-ui/Paper';
import Avatar from 'material-ui/Avatar';
import CircularProgress from 'material-ui/CircularProgress';



var Chip = require('material-ui/Chip').default;

import 'brace/mode/java';
import 'brace/theme/github';

import {
  Step,
  StepButton,
  Stepper,
  StepLabel,
  StepContent,
} from 'material-ui/Stepper';



function mapStateToProps(state) {
  return {
  };
}

function mapDispatchToProps(dispatch) {
  return {
  };
}

class Test extends React.Component<any, any> {
  constructor(props){
    super(props)

    var i = 0

    console.log(this.props)

    if(this.props.rejected === undefined && this.props.accepted === undefined)
      i = 0

    //Pending
    if(this.props.rejected === false && this.props.accepted === false)
      i = 1

    if(this.props.rejected)
      i = 1  

    if(this.props.accepted)
      i = 2  

    this.state = {
      finished: false,
      stepIndex: i
    }
  }

  handleNext = () => {
    const {stepIndex} = this.state;
    console.log(stepIndex)
    this.setState({
      stepIndex: stepIndex + 1,
      finished: stepIndex >= 2,
    });
  };

  handlePrev = () => {
    const {stepIndex} = this.state;
    if (stepIndex > 0) {
      this.setState({stepIndex: stepIndex - 1});
    }
  };

  renderStepActions(step) {
    const stepIndex = this.state.stepIndex;

    return (
      <div style={{margin: '12px 0'}}>
        <RaisedButton
          label={stepIndex === 2 ? 'Finish' : 'Next'}
          disableTouchRipple={true}
          disableFocusRipple={true}
          primary={true}
          onTouchTap={this.handleNext}
          style={{marginRight: 12}}
        />
        {step > 0 && (
          <FlatButton
            label="Back"
            disabled={stepIndex === 0}
            disableTouchRipple={true}
            disableFocusRipple={true}
            onTouchTap={this.handlePrev}
          />
        )}
      </div>
    );
  }



  render(){
    return <div style={{width: "100%"}}>
      <MuiThemeProvider>
        <div>
          <AppBar
            title="The Game"
            iconClassNameRight="muidocs-icon-navigation-expand-more"
           /> 
           {this.stepper()}
         </div>
      </MuiThemeProvider>
    </div>
  }

  codeMaster(){
    return this.props.code_master
  }

  stepper(){
     return (<Stepper activeStep={this.state.stepIndex} orientation="vertical" linear={false}>
          <Step>
            <StepButton onTouchTap={()=>this.setState({stepIndex: 0})}>
              The Question
            </StepButton>
            <StepContent>
              <p>The code master for this puzzle is...</p>
              <p style={{fontWeight: 700}}>{this.codeMaster()}!</p>
              <p>Read the first line.  Direct your team accordingly.  Then go on to the next line.  And so on.</p>
              <AceEditor
                  mode="java"
                  theme="github"
                  onChange={()=>{}}
                  name="first"
                  defaultValue={this.props.question.code}
                  height="200px"
                />

              Press the button below when you have handled all the lines in the code.
                
              {this.renderStepActions(0)}
            </StepContent>
          </Step>
          <Step>
            <StepButton onTouchTap={()=>this.setState({stepIndex: 1})}>
              The Photo
            </StepButton>
            <StepContent>
              <p>
                Use the button below to submit a group photo (make sure that it clearly shows that everyone is in the right positions).  Then wait for the confirmation that you got the answer right.
              </p>
              
              <iframe src={this.props.backend_resource + "/submissions/new"} style={{border: "none", width:"100%", height: "300px"}}></iframe>


              {this.renderStepActions(1)}
            </StepContent>
          </Step>
          <Step>
            <StepButton onTouchTap={()=>this.setState({stepIndex: 2})}>
              The Clue
            </StepButton>
            <StepContent>
              {this.clue()}

              {this.renderStepActions(2)}
            </StepContent>
          </Step>
        </Stepper>)
  }


  componentDidMount(){
    setTimeout(function(){
      top.postMessage({test: "Data2"}, "*")
    }, 1000)
  }

  clue(){
    return <iframe src={this.props.backend_resource + "/clue"} style={{border: "none", width:"100%", height: "300px"}}></iframe>
  }
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Test);



