var easy = [

  {
  code: `
lineUp(["Alice","Bree","Carry","Deanna"], "North")
takeSteps("Alice", 3, "North")
`
.trim()
},

  {code: 
`
lineUp(["Alice","Bree","Carry","Deanna"], "North")
takeSteps("Deanna", 10, "North")
takeSteps("Alice", 10, "South")
takeSteps("Bree", 3, "North")
takeSteps("Carry", 3, "South")
`.trim()
},

  {code: 
`
lineUp(["Alice","Bree","Carry","Deanna"], "North")
takeSteps("Deanna", 10, "North")
takeSteps("Alice", 10, "South")
takeSteps("Bree", 3, "North")
takeSteps("Carry", 3, "South")
takeSteps("Deanna", 3, "East")
takeSteps("Alice", 3, "West")
standUp("Bree", false)
standUp("Carry", false)
`.trim()
},

  {code: 
`
lineUp(["Alice","Bree","Carry","Deanna"], "East")
standUp("Bree", false)
standUp("Carry", false)
swap("Alice", "Bree")
swap("Deanna", "Carry")
`.trim()
},

  {code: 
`
lineUp(["Alice","Bree","Carry","Deanna"], "South")
write("Alice", "I")
write("Bree", "am")
write("Carry", "a")
write("Deanna", "coder")
`.trim()
},

  {code: 
`
lineUp(["Alice","Bree","Carry","Deanna"], "West")
write("Alice", 1)
write("Bree", 2)
write("Carry", 3)
write("Deanna", 4)
swap("Bree", "Alice")
swap("Carry", "Alice")
swap("Deanna", "Alice")
`.trim()
},

  {code: 
`
lineUp(["Alice","Bree","Carry","Deanna"], "South")
write("Alice", "I")
takeSteps("Alice", 1, "South")
write("Bree", "am")
takeSteps("Bree", 2, "South")
write("Carry", "a")
takeSteps("Carry", 3, "South")
write("Deanna", "CODER!")
takeSteps("Deanna", 4, "South")
`.trim()
},

  {code: 
`
lineUp(["Alice","Bree","Carry","Deanna"], "North")
write("Alice", "I")
write("Bree", "am")
write("Carry", "a")
write("Deanna", "coder")
takeSteps("Deanna", 4, "North")
takeSteps("Carry", 4, "North")
takeSteps("Alice", 7, "East")
takeSteps("Bree", 7, "East")
takeSteps("Alice", 4, "North")
takeSteps("Bree", 4, "North")
write("Carry", "A")
write("Bree", "am!")
`.trim()
},

]

export default easy



